import React, { useCallback, useContext, useEffect, useMemo, useState } from 'react'
import { CardBody, ArrowDownIcon, Button, IconButton, Image, Text } from '@pancakeswap-libs/uikit'
import styled from 'styled-components'
import axios from 'axios'
import Web3 from 'web3'
import { ROUTER_ADDRESS } from '../../../constants/index'
import useGetContract from '../../../tokens/useGetContract'
import useGetTokenInfo from '../../../tokens/useGetTokenInfo'
import pancakeRouterAbi from '../../../constants/abis/pancakeRounter.json'
import pancakeFactoryv1Abi from '../../../constants/abis/pancakeFactoryv1.json'
import pancakeFactoryv2Abi from '../../../constants/abis/pancakeFactoryv2.json'
import { TransactionLink, TYPE } from '../../../components/Shared'

const StyledTokenInfoArea = styled.div`
  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.tertiary};
  width: 15%;
  margin-right: 10px;
`

const StyledTokenLogo = styled(Image)`
  margin-top: 20px;
  margin-bottom: 20px;
`
const StyledTokenInfoItem = styled.div`
  padding: 10px 15px;
`

const StyledTitle = styled(Text)`
  font-family: Poppins;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
`

const StyledDetail = styled(Text)`
  font-family: Poppins;
  font-style: normal;
  font-size: 24px;
  line-height: 30px;
`

const StyledChangeDetail = styled(Text)`
  font-family: Poppins;
  font-style: normal;
  font-size: 24px;
  line-height: 30px;
  color: rgb(27, 200, 112);
`

interface TokenInfoProps {
  name: string | null
  address: string
  symbol: string | null
}

const web3 = new Web3(new Web3.providers.HttpProvider('https://bsc-dataseed1.defibit.io'))

const pancakeFactoryV1 = '0xBCfCcbde45cE874adCB698cC183deBcF17952812'
const pancakeFactoryV2 = '0xcA143Ce32Fe78f1f7019d7d551a6402fC5350c73'
const pancakeRouterContract = new web3.eth.Contract(pancakeRouterAbi as any, ROUTER_ADDRESS)
const pancakeFactoryV1Contract = new web3.eth.Contract(pancakeFactoryv1Abi as any, pancakeFactoryV1)
const pancakeFactoryV2Contract = new web3.eth.Contract(pancakeFactoryv2Abi as any, pancakeFactoryV2)

let pancakeSwapLP = '0x0c5DA0f07962dd0256c079248633f2b43CaD6f62'
let pancakeSwapLPv1 = '0x746a3f1a3863cf839bf0702c083cca888aba6ee8'
const wbnb = '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c'
const topicForTransfer = '0xddf252ad1be2c89b69c2b068fc378daa952ba7f163c4a11628f55a4df523b3ef'

const BaseUrl = 'https://dex-api.windswap.finance'

const StyledTradingHistoryArea = styled.div`
  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.tertiary};
  width: 100%;
  padding: 15px;
`

const StyledTradingHistoryTitle = styled(Text)`
  font-family: Poppins;
  font-style: normal;
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 15px;
`

const StyledTable = styled.table`
  width: 100%;
  color: #fff;
`
const StyledThead = styled.thead`
  padding: 5px;
  border-bottom: 2px solid white;
`

const StyledTH = styled.th`
  padding-bottom: 10px;
  color: #868686;
  font-weight: 200;
  font-size: 18px;
  text-align: left;
`

const StyledTD = styled.td`
  border-bottom: 1px solid #ffffff;
  padding: 10px;
  padding-left: 0px;
`

const StyledBuyItem = styled.tr`
  color: rgb(27, 200, 112);
`

const StyledSellItem = styled.tr`
  color: #ff3939;
`

const StyledTBody = styled.tbody`
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 5px;
  height: 320px;
  min-height: 320px;
  max-height: 320px;
`

const TransactionHistory = ({ name, address, symbol }: TokenInfoProps) => {
  const tokenName = name
  const tokenAddress = address
  const tokenSymbol = symbol

  const [transactions, setTransactions] = useState<any>([])
  const [trxHashes, setTrxHashes] = useState<any>([])
  const [bnbPrice, setBnbPrice] = useState(0)

  const { contract, getContract } = useGetContract()
  const { tokenInfo, getTokenInfo } = useGetTokenInfo()

  const updatePairAddresses = async (_address: string) => {
    pancakeSwapLPv1 = await pancakeFactoryV1Contract.methods.getPair(wbnb, _address).call()
    pancakeSwapLP = await pancakeFactoryV2Contract.methods.getPair(wbnb, _address).call()
  }

  useEffect(() => {
    updatePairAddresses(tokenAddress as string)

    function getTime(time) {
      const timeArray = time.toString().split(':')
      if (Number(timeArray[0]) > 12) {
        timeArray[0] = (Number(timeArray[0]) % 12).toString()
      }
      return timeArray.join(':')
    }

    function getAMPM(time) {
      const timeArray = time.toString().split(':')
      let ampm = 'AM'
      if (Number(timeArray[0]) > 12) {
        ampm = 'PM'
      }
      return ampm
    }

    setTransactions([])
    function getLast50Transactions() {
      axios.post(`${BaseUrl}/api/v1/get_last_50_transactions`, { token: tokenAddress }).then((res) => {
        if (res.data.status === 200) {
          const _transactions = res.data.data

          setTrxHashes(_transactions.map((each) => each.transactionHash))

          setTransactions(
            _transactions.map((each) => ({
              address: each.address,
              direction: each.side === 'BUY' ? 'SELL' : 'BUY',
              lp: each.protocol,
              price: each.amount2.toString().slice(0, 8),
              price_token: (each.amount2 / each.amount1).toString().slice(0, 8),
              time: { strTime: getTime(each.time), ampm: getAMPM(each.time) },
              timestamp: each.timestamp,
              token: each.amount1.toString().slice(0, 8),
              transactionHash: each.hash,
            }))
          )
        }
      })
    }
    getLast50Transactions()

    getContract(tokenAddress)
    getTokenInfo(tokenAddress)
  }, [getContract, getTokenInfo, tokenAddress])

  const getBNBAmountFromTx = React.useCallback((txHash: string) => {
    return new Promise((resolve, reject) => {
      web3.eth
        .getTransactionReceipt(txHash)
        .then((transactionReceipt) => {
          let bnbAmount = 0
          web3.eth
            .getBlock(transactionReceipt.blockNumber)
            .then((blockTimeStamp: any) => {
              const time = new Date(blockTimeStamp.timestamp * 1000)

              transactionReceipt.logs.forEach((log: any) => {
                if (log.topics[0] === topicForTransfer && log.address === wbnb) {
                  bnbAmount = (log.data * 1.0) / 10 ** 18
                  resolve([bnbAmount, formatAMPM(time), blockTimeStamp.timestamp])
                }
              })
            })
            .catch((blockTimeStampErr) => {
              console.log({
                blockTimeStampErr,
              })
              resolve([0, 0, 0])
            })
        })
        .catch((transactionReceiptErr) => {
          console.log({
            transactionReceiptErr,
          })

          resolve([0, 0, 0])
        })
    })
  }, [])

  const processTransaction = React.useCallback(
    (transactionHash, returnValues, decimal) => {
      return new Promise((resolve, reject) => {
        let trxInfo = {}
        if (
          returnValues.to.toLowerCase() === pancakeSwapLP.toLowerCase() ||
          returnValues.to.toLowerCase() === pancakeSwapLPv1.toLowerCase() ||
          returnValues.from.toLowerCase() === pancakeSwapLP.toLowerCase() ||
          returnValues.from.toLowerCase() === pancakeSwapLPv1.toLowerCase()
        ) {
          getBNBAmountFromTx(transactionHash).then((res: any) => {
            const [bnbAmount, timeString, timeStamp] = res
            if (
              returnValues.to.toLowerCase() === pancakeSwapLP.toLowerCase() ||
              returnValues.to.toLowerCase() === pancakeSwapLPv1.toLowerCase()
            ) {
              trxInfo = {
                direction: 'SELL',
                transactionHash,
                address: returnValues.from,
                token: ((returnValues.value * 1.0) / 10 ** (decimal * 1.0)).toFixed(4),
                price: bnbAmount.toFixed(4),
                time: timeString,
                timeStamp,
                price_token: bnbAmount / ((returnValues.value * 1.0) / 10 ** (decimal * 1.0)),
                lp: returnValues.to.toLowerCase() === pancakeSwapLP.toLowerCase() ? 'LP v2' : 'LP v1',
              }
            } else if (
              returnValues.from.toLowerCase() === pancakeSwapLP.toLowerCase() ||
              returnValues.from.toLowerCase() === pancakeSwapLPv1.toLowerCase()
            ) {
              trxInfo = {
                direction: 'BUY',
                transactionHash,
                address: returnValues.to,
                token: ((returnValues.value * 1.0) / 10 ** (decimal * 1.0)).toFixed(4),
                price: bnbAmount.toFixed(4),
                time: timeString,
                timeStamp,
                price_token: bnbAmount / ((returnValues.value * 1.0) / 10 ** (decimal * 1.0)),
                lp: returnValues.from.toLowerCase() === pancakeSwapLP.toLowerCase() ? 'LP v2' : 'LP v1',
              }
            }

            resolve(trxInfo)
          })
        }
      })
    },
    [getBNBAmountFromTx]
  )

  useEffect(() => {
    const getBnbPriceInterval = setInterval(() => {
      getBNBPrice()
    }, 2000)
    return () => clearInterval(getBnbPriceInterval)
  }, [bnbPrice])

  const getTransactions = React.useCallback(() => {
    if (!contract || !tokenInfo) return

    const { decimals } = tokenInfo

    web3.eth.getBlockNumber().then((currentBlock) => {
      contract.getPastEvents(
        {
          filter: {},
          fromBlock: currentBlock * 1.0 - 1000,
          toBlock: currentBlock * 1.0,
          topics: ['0xddf252ad1be2c89b69c2b068fc378daa952ba7f163c4a11628f55a4df523b3ef'],
        },
        function (error: any, events: any) {
          if (error) {
            // setTransactions([]);
          } else {
            for (let i = 0; i < events.length; i++) {
              const eachTransfer = events[i]
              if (eachTransfer.raw) {
                const { raw, transactionHash } = eachTransfer
                if (!trxHashes.includes(transactionHash)) {
                  setTrxHashes((oldTrxHashes) => {
                    return [...oldTrxHashes, transactionHash]
                  })

                  const { data, topics } = raw
                  const value = data * 1.0
                  const from = topics[1].replace('0x000000000000000000000000', '0x')
                  const to = topics[2].replace('0x000000000000000000000000', '0x')

                  if (
                    from.toLowerCase() === pancakeSwapLP.toLowerCase() ||
                    to.toLowerCase() === pancakeSwapLP.toLowerCase() ||
                    from.toLowerCase() === pancakeSwapLPv1.toLowerCase() ||
                    to.toLowerCase() === pancakeSwapLPv1.toLowerCase()
                  ) {
                    processTransaction(transactionHash, { from, to, value }, decimals)
                      .then((trxInfo) => {
                        setTransactions((oldTransactions) => {
                          const trxs = [trxInfo, ...oldTransactions]
                          trxs.sort((a, b) => b.timeStamp - a.timeStamp)
                          if (trxs.length > 50) return trxs.slice(0, 50)
                          return trxs
                        })
                      })
                      .catch((trxInfoErr) => {
                        console.log({
                          trxInfoErr,
                        })
                      })
                  }
                }
              }
            }
          }
        }
      )
    })
  }, [processTransaction, trxHashes, contract, tokenInfo])

  useEffect(() => {
    const getTransactionsInterval = setInterval(() => {
      getTransactions()
    }, 4000)
    return () => clearInterval(getTransactionsInterval)
  }, [getTransactions])

  const getCellStyleByAction = (direction: string) => {
    return direction === 'BUY' ? 'text-success' : 'text-danger'
  }

  const getUSDAmount = (bnb: any) => {
    return `$${(bnb * bnbPrice).toFixed(2)}`
  }

  const getBNBPrice = () => {
    pancakeRouterContract.methods
      .getAmountsOut((10 ** 18).toString(), [
        '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c',
        '0x8AC76a51cc950d9822D68b83fE1Ad97B32Cd580d',
      ])
      .call()
      .then((amountsOut) => {
        const amountOut: any = web3.utils.fromWei(amountsOut[1])

        setBnbPrice(amountOut)
      })
      .catch((error) => {
        console.log(error)
        setBnbPrice(0)
      })
  }

  const formatAMPM = (date: any) => {
    date = new Date(date)
    let hours = date.getHours()
    let minutes = date.getMinutes()
    let secs = date.getSeconds()
    const ampm = hours >= 12 ? 'PM' : 'AM'
    hours %= 12
    hours = hours !== 0 ? hours : 12
    minutes = minutes < 10 ? `0${minutes}` : minutes
    secs = secs < 10 ? `0${secs}` : secs
    const strTime = `${hours}:${minutes}:${secs}`
    return { strTime, ampm }
  }

  return (
    <StyledTradingHistoryArea>
      <StyledTradingHistoryTitle>Trading History</StyledTradingHistoryTitle>
      <StyledTable>
        <StyledThead>
          <tr>
            <StyledTH>Time</StyledTH>
            <StyledTH>Traded</StyledTH>
            <StyledTH>Token Price</StyledTH>
            <StyledTH>Value</StyledTH>
            <StyledTH>Transaction</StyledTH>
          </tr>
        </StyledThead>
        <StyledTBody>
          {transactions !== null &&
            transactions.map((transaction, index) => {
              if (index < 10) {
                return transaction.direction.toLowerCase() === 'buy' ? (
                  <StyledBuyItem>
                    <StyledTD>
                      {transaction.time.strTime} {transaction.time.ampm}
                    </StyledTD>
                    <StyledTD>{transaction.token.toString().slice(0, 8)}</StyledTD>
                    <StyledTD>{getUSDAmount(transaction.price)}</StyledTD>
                    <StyledTD>{getUSDAmount(transaction.price_token)}</StyledTD>
                    <StyledTD>
                      <TransactionLink
                        href={`https://bscscan.com/tx/${transaction.transactionHash}`}
                        style={{ fontSize: '14px' }}
                      >
                        {transaction.transactionHash.substring(0, 12)}...
                      </TransactionLink>
                    </StyledTD>
                  </StyledBuyItem>
                ) : (
                  <StyledSellItem>
                    <StyledTD>
                      {transaction.time.strTime} {transaction.time.ampm}
                    </StyledTD>
                    <StyledTD>{transaction.token.toString().slice(0, 8)}</StyledTD>
                    <StyledTD>{getUSDAmount(transaction.price)}</StyledTD>
                    <StyledTD>{getUSDAmount(transaction.price_token)}</StyledTD>
                    <StyledTD>
                      <TransactionLink
                        href={`https://bscscan.com/tx/${transaction.transactionHash}`}
                        style={{ fontSize: '14px' }}
                      >
                        {transaction.transactionHash.substring(0, 12)}...
                      </TransactionLink>
                    </StyledTD>
                  </StyledSellItem>
                )
              }
              return <></>
            })}
        </StyledTBody>
      </StyledTable>
    </StyledTradingHistoryArea>
  )
}

export default TransactionHistory
