import React, { useCallback, useContext, useEffect, useMemo, useState } from 'react'
import { CardBody, ArrowDownIcon, Button, IconButton, Image, Text } from '@pancakeswap-libs/uikit'
import styled from 'styled-components'
import { TokenInfo } from '@uniswap/token-lists'
import axios from 'axios'
import TokenInfoContainer from './components/TokenInfoContainer'
import TransactionHistory from './components/TransactionHistory'
import { isAddress } from '../../utils'

const TokenAddressInput = styled.input`
  position: relative;
  display: flex;
  padding: 10px;
  align-items: center;
  width: 85%;
  white-space: nowrap;
  background: none;
  border: none;
  outline: none;
  border-radius: 8px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.tertiary};
  -webkit-appearance: none;

  font-size: 18px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    border: 1px solid ${({ theme }) => theme.colors.primary};
    outline: none;
  }
`

const StyledDataArea = styled.div`
  width: 100%;
  display: flex;
  flex-flow: row;
  margin: 10px;
`

const StyledChartArea = styled.div`
  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.tertiary};
  width: 60%;
`

const StyledADArea = styled.div`
  margin-left: 10px;
  border-radius: 8px;
  border: 1px solid ${({ theme }) => theme.colors.tertiary};
  width: 24%;
  padding: 15px;
`

const StyledSearchBar = styled.div`
  width: 40%;
  display: flex;
  flex-flow: row;
  margin-bottom: 20px;
`

const StyledButton = styled(Button)`
  margin-left: 10px;
  width: 15%;
`

const Chart = () => {
  let sessionToken = sessionStorage.getItem('search_token')
  if (sessionToken == null) {
    sessionToken = '0xd1587ee50e0333f0c4adcf261379a61b1486c5d2'
  }
  const [tokenName, setTokenName] = useState<string | null>('')
  const [tokenSymbol, setTokenSymbol] = useState<string | null>('')
  const [tokenAddress, setTokenAddress] = useState<string>(sessionToken)

  const BaseUrl = 'https://dex-api.windswap.finance'

  if (document.createEvent) {
    const event = document.createEvent('HTMLEvents') // The custom event that will be created
    event.initEvent('makeChart', true, true)
    window.dispatchEvent(event)
  }

  useEffect(() => {
    setTokenName(sessionStorage.getItem('search_token_name'))
    setTokenSymbol(sessionStorage.getItem('search_token_symbol'))
    axios.post(`${BaseUrl}/api/v1/search_token`, { index: tokenAddress }).then((res) => {
      if (res.data.status === 200) {
        const tokenList = res.data.data
        console.log('pooh token search  = ', tokenList[0])

        sessionStorage.setItem('search_token_name', tokenList[0].name)
        sessionStorage.setItem('search_token_symbol', tokenList[0].symbol)
        sessionStorage.setItem('search_token', tokenList[0].address)
      }
    })
  }, [tokenAddress])

  function searchToken() {
    window.location.reload()
  }

  function buyToken() {
    window.open('https://exchange.pancakeswap.finance/#/swap?outputCurrency='.concat(tokenAddress), '_blank')
  }

  const handleInput = useCallback((event) => {
    const input = event.target.value
    const checksummedInput = isAddress(input)
    setTokenAddress(checksummedInput || input)
  }, [])

  return (
    <>
      <StyledSearchBar>
        <TokenAddressInput
          type="text"
          id="token-address-input"
          placeholder="Input Token Address"
          value={tokenAddress}
          onChange={handleInput}
        />
        <StyledButton disabled={false} onClick={searchToken} fullWidth>
          Search
        </StyledButton>
        <StyledButton disabled={false} onClick={buyToken} fullWidth>
          Buy
        </StyledButton>
      </StyledSearchBar>
      <StyledDataArea>
        <TokenInfoContainer name={tokenName} address={tokenAddress} symbol={tokenSymbol} />
        <StyledChartArea id="tv_chart_container" />
        <StyledADArea>
          <Image src="images/Background.png" width={350} height={580} responsive />
        </StyledADArea>
      </StyledDataArea>
      <TransactionHistory name={tokenName} address={tokenAddress} symbol={tokenSymbol} />
    </>
  )
}
export default Chart
